import { request } from "./request";
import Vue from 'vue'

export function pointList(elecEqmId) {
    return request({
        url: "/web/apps/gongqi.hsb.elec/elecEqmTable/pointList?elecEqmId=" + elecEqmId,
        method: 'GET'
    })
}
export function energyPointList(elecEqmId) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyEqmTable/pointList?eqmId=" + elecEqmId,
        method: 'GET'
    })
}
export function pvPointList(pvEqmId) {
    return request({
        url: "/web/apps/gongqi.hsb.pv/pvEqmTable/pointList?pvEqmId=" + pvEqmId,
        method: 'GET'
    })
}
export function pvPointQuery(pvEqmId,fromDate,toDate) {
    return request({
        url: "/web/apps/gongqi.hsb.pv/pvEqmTable/pointQuery?pvEqmId=" + pvEqmId + '&fromDate='+fromDate+'&toDate='+toDate,
        method: 'GET'
    })
}
export function energyPointQuery(pvEqmId,fromDate,toDate) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyEqmTable/pointQuery?eqmId=" + pvEqmId+ '&fromDate='+fromDate+'&toDate='+toDate,
        method: 'GET'
    })
}
export function elecPointQuery(pvEqmId,fromDate,toDate) {
    return request({
        url: "/web/apps/gongqi.hsb.elec/elecEqmTable/pointQuery?elecEqmId=" + pvEqmId+ '&fromDate='+fromDate+'&toDate='+toDate,
        method: 'GET'
    })
}
