import { request } from "./request";
import Vue from 'vue'

export function pvEqmTableList(id,type) {
    return request({
        url: "/web/apps/gongqi.hsb.pv/pvEqmTable/list?id=" + id + '&type=' + type,
        method: 'GET'
    })
}
