<template>
  <div class="parameterManage">
    <div class="left border-box tree">
      <el-tree
        :default-expand-all="true"
        :expand-on-click-node="false"
        :data="data"
        :props="defaultProps"
        @node-click="nodeClick"
      ></el-tree>
    </div>
    <div class="table">
      <div class="tabs">
        <div
          v-for="(item, index) in tabsArry"
          :key="index"
          :class="{ isTabActive: index == tabIndex }"
          @click="changeTab(index)"
          v-show="item.show"
        >
          {{ item.name }}
        </div>
      </div>
      <div
        v-show="tabIndex >= 0"
        style="
          padding: 0.22rem;
          flex-grow: 1;
          display: flex;
          flex-direction: column;
        "
      >
        <div style="flex-shink: 0">
          <el-table
            :data="tableData"
            style="width: 100%"
            height="250"
            stripe
            @row-click="rowClick"
          >
            <template v-for="(item, index) in tableHeader">
              <el-table-column :prop="item.key" :label="item.name" :key="index">
              </el-table-column>
            </template>
          </el-table>
        </div>
        <div class="pointBlock">
          <div
            style="display: flex; align-items: center; margin-bottom: 0.2rem"
          >
            <p style="flex-grow: 1">测量点</p>
            <div style="display: flex; align-items: center">
              <div>
                <el-date-picker
                  v-model="date1"
                  type="date"
                  placeholder=""
                  value-format="yyyy-MM-dd"
                  :clearable="false"
                >
                </el-date-picker>
              </div>
              <div style="color: #00b4fe; padding: 0 0.2rem">至</div>
              <div>
                <el-date-picker
                  v-model="date2"
                  type="date"
                  placeholder=""
                  value-format="yyyy-MM-dd"
                  :clearable="false"
                >
                </el-date-picker>
              </div>
              <button class="searchBth" @click="pointQueryFun()">查询</button>
            </div>
          </div>
          <div style="flex-grow: 1">
            <el-table
              :data="subTableData"
              style="width: 100%"
              height="100%"
              stripe
            >
              <template v-for="(item, index) in subTableHeader">
                <el-table-column
                  :prop="item.key"
                  :label="item.name"
                  :key="index"
                >
                </el-table-column>
              </template>
            </el-table>
          </div>
        </div>
      </div>
    </div>
    <pointDialog ref="pDialog"></pointDialog>
  </div>
</template>
<script>
import {
  elecEqmTableList,
  energyEqmTableList,
  pvEqmTableList,
} from "@/network/parameterManage";
import {
  pointList,
  energyPointList,
  pvPointList,
  pvPointQuery,
  energyPointQuery,
  elecPointQuery,
} from "@/network/pointQuery";
import pointDialog from "./components/point_dialog.vue";
import { places, pvPlaces, elePlaces } from "@/network/energyTrendAnalysis";
export default {
  components: {
    pointDialog,
  },
  data() {
    return {
      data: [],
      defaultProps: {
        children: "children",
        label: "label",
      },
      tabsArry: [
        {
          name: "配电房",
          show: false,
        },
        {
          name: "能源",
          show: false,
        },
        {
          name: "光伏",
          show: false,
        },
      ],
      tabIndex: -1,
      tableData: [],
      tableHeader: [],
      subTableData: [],
      subTableHeader: [],
      date1: "",
      date2: "",
      rowData: {},
    };
  },
  mounted() {
    this.elePlacesFun();

    let menuPermissions = JSON.parse(sessionStorage.getItem("menuPermissions"));
    this.tabsArry = [
      {
        name: "配电房",
        show: menuPermissions.isPDF,
      },
      {
        name: "能源",
        show: menuPermissions.isNY,
      },
      {
        name: "光伏",
        show: menuPermissions.isGF,
      },
    ];
    if (menuPermissions.isPDF) {
      this.tabIndex = 0;
    } else if (menuPermissions.isNY) {
      this.tabIndex = 1;
    } else if (menuPermissions.isGF) {
      this.tabIndex = 2;
    }

    this.date1 = this.date2 =
      this.$getDate().year +
      "-" +
      this.$getDate().month +
      "-" +
      this.$getDate().day;
  },
  methods: {
    changeTab(index) {
      this.tabIndex = index;
      if (index == 0) {
        this.elePlacesFun();
        // this.elecEqmTableListFun();
      } else if (index == 1) {
        this.placesFun();
      } else {
        this.pvPlacesFun();
        // this.pvEqmTableListFun();
      }
    },
    /**
     * 能源获取树形结构列表
     * **/
    placesFun() {
      places()
        .then((data) => {
          let flag = false;
          let res = data.content;
          res.forEach((item) => {
            if (!flag) {
              this.areaId = item.id;
              this.areaType = item.type;
              flag = true;
            }
          });
          this.data = this.$arrToTree(res);
          this.energyEqmTableListFun();
        })
        .catch((err) => {});
    },
    /**
    /**
     * 配电房获取树形结构列表
     * **/
    elePlacesFun() {
      elePlaces()
        .then((data) => {
          let flag = false;
          let res = data.content;
          res.forEach((item) => {
            if (!flag) {
              this.areaId = item.id;
              this.areaType = item.type;
              flag = true;
            }
          });
          this.data = this.$arrToTree(res);
          console.log(this.data);
          this.elecEqmTableListFun();
        })
        .catch((err) => {});
    },
    /**
     * 光伏获取树形结构列表
     * **/
    pvPlacesFun() {
      pvPlaces()
        .then((data) => {
          let flag = false;
          let res = data.content;
          res.forEach((item) => {
            if (!flag) {
              this.areaId = item.id;
              this.areaType = item.type;
              flag = true;
            }
          });
          this.data = this.$arrToTree(res);
          console.log(this.data);
          this.pvEqmTableListFun();
        })
        .catch((err) => {});
    },
    /**
     * 点击树形某一个节点
     * **/
    nodeClick(item) {
      this.areaId = item.id;
      this.areaType = item.type;
      if (this.tabIndex == 0) {
        this.elecEqmTableListFun();
      } else if (this.tabIndex == 1) {
        this.energyEqmTableListFun();
      } else {
        this.pvEqmTableListFun();
      }
    },
    /**
     * 获取配电房管理table数据
     * **/
    elecEqmTableListFun() {
      elecEqmTableList(this.areaId, this.areaType)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.tableData = content;
            this.tableHeader = [
              {
                name: "园区编号",
                key: "areaId",
              },
              {
                name: "企业编号",
                key: "companyId",
              },
              {
                name: "电站名称",
                key: "elecStationId",
              },
              {
                name: "位置",
                key: "position",
              },
              {
                name: "设备编号",
                key: "elecEqmId",
              },
              {
                name: "设备名称",
                key: "elecEqmName",
              },
              {
                name: "设备状态",
                key: "elecEqmState",
              },
              {
                name: "IDPOWERSUPPLY",
                key: "iDPOWERSUPPLY",
              },
            ];
            this.rowData = content[0] ? content[0] : {};
            let elecEqmId = content[0] ? content[0].elecEqmId : "";
            this.pointListFun(elecEqmId);
          }
        })
        .catch((err) => {});
    },
    /**
     * 获取能源管理table数据
     * **/
    energyEqmTableListFun() {
      energyEqmTableList(this.areaId, this.areaType)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.tableData = content;
            this.tableHeader = [
              {
                name: "设备类型",
                key: "energyType",
              },
              {
                name: "园区编号",
                key: "areaId",
              },
              {
                name: "企业编号",
                key: "companyId",
              },
              {
                name: "电站编号",
                key: "energyStationId",
              },
              {
                name: "位置",
                key: "position",
              },
              {
                name: "设备编号",
                key: "equipmentId",
              },
              {
                name: "设备名称",
                key: "equipmentName",
              },
              {
                name: "设备状态",
                key: "energyEqmState",
              },
              {
                name: "IDPOWERSUPPLY",
                key: "iDPOWERSUPPLY",
              },
            ];
            this.rowData = content[0] ? content[0] : {};
            let equipmentId = content[0] ? content[0].equipmentId : "";
            this.energyPointListFun(equipmentId);
          }
        })
        .catch((err) => {});
    },
    /**
     * 获取光伏管理table数据
     * **/
    pvEqmTableListFun() {
      pvEqmTableList(this.areaId, this.areaType)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.tableData = content;
            this.tableHeader = [
              {
                name: "企业编号",
                key: "companyId",
              },
              {
                name: "设备编号",
                key: "pVEqmId",
              },
              {
                name: "设备名称",
                key: "pVEqmName",
              },
              {
                name: "设备类型",
                key: "pVEqmType",
              },
              {
                name: "IDPOWERSUPPLY",
                key: "iDPOWERSUPPLY",
              },
              {
                name: "气象仪",
                key: "meteorographEqmId",
              },
            ];
            this.rowData = content[0] ? content[0] : {};
            let pVEqmId = content[0] ? content[0].pVEqmId : "";
            this.pvPointListFun(pVEqmId);
          }
        })
        .catch((err) => {});
    },
    /**
     * 点击表格获取测量点数据
     * **/
    rowClick(row) {
      this.rowData = row;
      if (this.tabIndex == 0) {
        this.pointListFun(row.elecEqmId);
      } else if (this.tabIndex == 1) {
        this.energyPointListFun(row.equipmentId);
      } else {
        this.pvPointListFun(row.pVEqmId);
      }
    },
    /**
     * 获取配电房测点数据
     * **/
    pointListFun(elecEqmId) {
      this.subTableHeader = [
        {
          name: "测量点名称",
          key: "pointName",
        },
        {
          name: "测量点类型",
          key: "elecPointType",
        },
        {
          name: "POINT_NO",
          key: "pOINT_NO",
        },
        {
          name: "上限设定",
          key: "limitHighValue",
        },
        {
          name: "下限设定",
          key: "limitLowValue",
        },
        {
          name: "测量点单位",
          key: "elecPointUnitId",
        },
      ];
      if (elecEqmId == "") {
        this.subTableData = [];
        return;
      }
      pointList(elecEqmId)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.subTableData = content;
          }
        })
        .catch((err) => {});
    },
    /**
     * 获取能源管理测点数据
     * **/
    energyPointListFun(elecEqmId) {
      this.subTableHeader = [
        {
          name: "测量点名称",
          key: "pointName",
        },
        {
          name: "蒸汽表类型",
          key: "steamPointType",
        },
        {
          name: "POINT_NO",
          key: "pOINT_NO",
        },
        {
          name: "上限设定",
          key: "limitHighValue",
        },
        {
          name: "下限设定",
          key: "limitLowValue",
        },
        {
          name: "测量点单位",
          key: "energyPointUnitId",
        },
      ];
      if (elecEqmId == "") {
        this.subTableData = [];
        return;
      }
      energyPointList(elecEqmId)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.subTableData = content;
          }
        })
        .catch((err) => {});
    },
    /**
     * 获取光伏管理测点数据
     * **/
    pvPointListFun(elecEqmId) {
      this.subTableHeader = [
        {
          name: "测量点名称",
          key: "pointName",
        },
        {
          name: "测量点类型",
          key: "elecMeterPointType",
        },
        {
          name: "POINT_NO",
          key: "pOINT_NO",
        },
        {
          name: "电流电压标准值",
          key: "standardValue",
        },
        {
          name: "上限设定",
          key: "limitHighValue",
        },
        {
          name: "下限设定",
          key: "limitLowValue",
        },
        {
          name: "测量点单位",
          key: "pVPointType",
        },
      ];
      if (elecEqmId == "") {
        this.subTableData = [];
        return;
      }
      pvPointList(elecEqmId)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.subTableData = content;
          }
        })
        .catch((err) => {});
    },
    /**
     * 查询按钮
     * **/
    pointQueryFun() {
      if (this.tabIndex == 0) {
        this.elePointQueryFun(this.rowData.elecEqmId);
      } else if (this.tabIndex == 1) {
        this.energyPointQueryFun(this.rowData.equipmentId);
      } else {
        this.pvPointQueryFun(this.rowData.pVEqmId);
      }
    },
    /**
     * 光伏电站查询
     * **/
    pvPointQueryFun(eqmId) {
      pvPointQuery(eqmId, this.date1, this.date2)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            let arr = [];
            content.forEach((item) => {
              arr.push({
                name: item.pointName,
                POINT_NO: item.POINT_NO,
                COLL_DATE: item.COLL_DATE,
                Value: item.Value,
                UnitCHN: item.UnitCHN,
              });
            });
            this.$refs.pDialog.showDialogFun(arr);
          }
        })
        .catch((err) => {});
    },
    /**
     * 能源电站查询
     * **/
    energyPointQueryFun(eqmId) {
      energyPointQuery(eqmId, this.date1, this.date2)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            let arr = [];
            content.forEach((item) => {
              arr.push({
                name: item.energyPointUnitId,
                POINT_NO: item.POINT_NO,
                COLL_DATE: item.COLL_DATE,
                Value: item.DATA_VALUE,
                UnitCHN: item.UnitId,
              });
            });
            this.$refs.pDialog.showDialogFun(arr);
          }
        })
        .catch((err) => {});
    },
    /**
     * 配电房电站查询
     * **/
    elePointQueryFun(eqmId) {
      elecPointQuery(eqmId, this.date1, this.date2)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            let arr = [];
            content.forEach((item) => {
              arr.push({
                name: item.elecPointUnitId,
                POINT_NO: item.POINT_NO,
                COLL_DATE: item.COLL_DATE,
                Value: item.Value,
                UnitCHN: item.UnitCHN,
              });
            });
            this.$refs.pDialog.showDialogFun(arr);
          }
        })
        .catch((err) => {});
    },
  },
};
</script>
<style scoped lang="less">
/deep/ .el-input__inner {
  background-color: rgba(0, 55, 146, 0.2);
  border: 1px solid rgba(34, 175, 237, 0.5);
}
.searchBth {
  background: #0066ff;
  color: #fff;
  border-radius: 0.05rem;
  padding: 0 0.47rem;
  border: none;
  cursor: pointer;
  height: 35px;
  margin-left: 0.2rem;
}
.left {
  width: 25%;
}
.search {
  display: flex;
  > div {
    margin-right: 0.48rem;
  }
  span {
    color: #00b4fe;
    margin-right: 0.2rem;
  }
}
.searchBth {
  background: #0066ff;
  color: #fff;
  border-radius: 0.05rem;
  padding: 0 0.47rem;
  border: none;
}
.parameterManage {
  background-image: url(@/assets/BG2.png);
  background-size: 100% 100%;
  display: flex;
  padding: 0.2rem;
  box-sizing: border-box;
  background-color: rgba(0, 15, 39, 1);
  width: 100%;
}
.table {
  flex-grow: 1;
  // margin-top: 0.16rem;
  margin-left: 0.2rem;
  border: 1px solid rgba(34, 175, 237, 0.5);
  background: rgba(0, 55, 146, 0.2);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.border-box {
  border: 1px solid rgba(34, 175, 237, 0.5);
  border-radius: 0.05rem;
  background: rgba(0, 55, 146, 0.2);
  display: flex;
  flex-direction: column;
}
.pointBlock {
  flex-grow: 1;
  margin-top: 0.2rem;
  display: flex;
  flex-direction: column;
  > div:first-child p {
    border-left: 5px solid rgba(0, 90, 228, 0.5);
    text-align: left;
    padding-left: 0.2rem;
    // margin-bottom: 0.2rem;
  }
}
/**表格**/
/deep/ .el-table,
/deep/ .el-table__expanded-cell,
/deep/ .el-table tr {
  background-color: transparent;
  color: #fff;
}
/deep/ .el-table th.el-table__cell {
  background-color: rgba(0, 90, 228, 0.5);
  color: #fff;
}
/deep/ .el-table td.el-table__cell,
/deep/ .el-table th.el-table__cell.is-leaf {
  border-bottom: none;
  text-align: center;
}
/deep/ .el-table--border::after,
/deep/ .el-table--group::after,
/deep/ .el-table::before {
  background-color: unset;
}
/deep/
  .el-table--striped
  .el-table__body
  tr.el-table__row--striped
  td.el-table__cell {
  background: rgba(0, 70, 176, 0.5);
  background-color: rgba(0, 70, 176, 0.5) !important;
}
/deep/
  .el-table--striped
  .el-table--enable-row-hover
  .el-table__body
  tr:hover
  > td.el-table__cell {
  background: rgba(0, 70, 176, 0.5);
}
/deep/
  .el-table--enable-row-hover
  .el-table__body
  tr:hover
  > td.el-table__cell {
  background-color: unset;
}

/**树形**/
.el-tree {
  background: #fff0;
  color: #fff;
  padding: 0.2rem;
  overflow: scroll;
}
.tree /deep/ .expanded.el-tree-node__expand-icon {
  color: #fff !important;
}
.tree /deep/ .el-tree-node:focus > .el-tree-node__content,
.tree /deep/ .el-tree-node__content:hover,
.el-upload-list__item:hover {
  background-color: rgba(0, 70, 176, 0.2) !important;
}
/deep/ .el-input__prefix {
  right: 5px;
  left: unset;
}
/deep/ .el-input__inner {
  color: #fff;
  height: 35px;
  line-height: 35px;
}
/deep/ .el-input__icon {
  color: #00b4fe;
  font-size: 18px;
  line-height: 35px;
}
/deep/ .el-date-editor.el-input,
/deep/ .el-date-editor.el-input__inner {
  width: 150px;
}
</style>