<template>
  <div>
    <el-dialog
      title=""
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      :modal="false"
      width="80%"
    >
      <div
        style="width: 100%; height: 6rem; margin-top: 0rem"
        class="dialog_content"
        v-if="dialogVisible"
      >
        <el-input
          placeholder="请输入内容"
          prefix-icon="el-icon-search"
          v-model="searchInput"
          style="margin-bottom: 0.15rem"
          @input="changeValue"
        >
        </el-input>
        <el-table :data="tableData" style="width: 100%" height="100%">
          <el-table-column prop="name" label="中文名">
          </el-table-column>
          <el-table-column prop="POINT_NO" label="POINT_NO"> </el-table-column>
          <el-table-column prop="COLL_DATE" label="COLL_DATE">
          </el-table-column>
          <el-table-column prop="Value" label="值"> </el-table-column>
          <el-table-column prop="UnitCHN" label="单位CHN"> </el-table-column>
        </el-table>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      searchInput: "",
      dialogVisible: false,
      tableData: [],
      tableDataBase: [],
    };
  },
  watch: {},
  mounted() {},
  methods: {
    changeValue(val) {
      if (val == "") {
        this.tableData = this.tableDataBase;
        return
      }
      let arr = [];
      this.tableDataBase.forEach((item) => {
        if (item.name.includes(val)) {
          arr.push(item);
        }
      });
      this.tableData = arr;
    },
    showDialogFun(content) {
      this.searchInput = ''
      this.dialogVisible = true;
      this.tableData = content;
      this.tableDataBase = content;
    },
  },
};
</script>
<style scoped lang="less">
/deep/ .el-dialog {
  background: #000000de;
}
/deep/ .el-dialog__headerbtn .el-dialog__close {
  color: #fff;
}
.search {
  display: flex;
  > div {
    margin-right: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
  }
  span {
    color: #00b4fe;
    margin-right: 0.2rem;
    white-space: nowrap;
  }
}
.dialog_content {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
/deep/ .el-form-item__label {
  color: #ffffff;
}
</style>